<template>
  <div :class="['choose-wrap', disabled?'disbaled-wrap':'']" @click="showWrapper" v-document-click="documentClick">
    <div :class="['choose', wclass + '-choose']">
      <span>{{value}}</span>
      <i class="el-icon-caret-bottom"></i>
      <div :class="{'mt-content': true, 'active': showWrapperActive}">
        <h2>{{title}}</h2>
        <div class="wrapper">
          <div class="col" v-for="(listData, index) in renderList" :key="index">
            <span
              :class="{'mt-item': true, 'active': item.name == value}"
              v-for="(item,index) in listData"
              :key="index"
              @click="changeValue(item)"
            >{{item.name}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // showWrapperActive: false
    };
  },
  props: ["list", "title", "value", "showWrapperActive", "disabled", "wclass"],
  created() {
    // console.log("test--" + this.class, this.disabled);
  },
  computed: {
    renderList: function() {
      let col = Math.ceil(this.list.length / 12);
      let resultList = [];
      for (let i = 0; i < col; i++) {
        let data = this.list.slice(i * 12, i * 12 + 12);
        resultList.push(data);
      }
      return resultList;
    }
  },
  methods: {
    showWrapper(e) {
      e.stopPropagation();
      // this.showWrapperActive = true;
      if(!this.disabled)
        this.$emit("change_active", true);
    },
    documentClick() {
      // this.showWrapperActive = false;
      this.$emit("change_active", false);
      // console.log("document-click");
    },
    changeValue(item) {
      this.$emit("change", item);
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/changeCity/select.scss";
    .disbaled-wrap {
        cursor: not-allowed;
    }
    .choose-wrap.disbaled-wrap > .choose {
        cursor: not-allowed;
    }
</style>


