            "http://p0.meituan.net/dpmerchantalbum/6c83d5727ce38eb0f73664ea4c376a92210951.jpg@275w_156h_1e_1c",
          title: "a",
          type: "food",
          score: 4.1,
          commentNum: 0,
          comment: [
            {
              username: "xxxx",
              evalaute: ""
            }
          ],
          tab: ["", ""],
          address: "35",
          avgPrice: 64,
          dealItems: [
            {
              title: "10-14",
              price: 909,
              counterPrice: 1150,
              saleNum: 0,
              priceType: ""
            }
          ]
        },
        {
          image:
            "http://p0.meituan.net/msmerchant/b34690f52ebdda221e4153c35878de8c2243192.jpg@275w_156h_1e_1c",
          title: "",
          type: "food",
          score: 5,
          commentNum: 2555,
          comment: [
            {
              username: "xxxx",
              evalaute: ""
            }
          ],
          tab: ["", ""],
          address: "16833055",
          avgPrice: 95,
          dealItems: [
            {
              title: "1001",
              price: 28.9,
              counterPrice: 100,
              saleNum: 3409,
              priceType: ""
            },
            {
              title: "WiFi",
              price: 280,
              counterPrice: 458,
              saleNum: 152,
              priceType: ""
            }
          ]
        }
      ]
    };
  },
  created() {
    api.getProductsList().then(res => {
      this.productList = res.data.data;
    });
  },
  components: {
      Item
  }
};
</script>

<style scoped>
