<template>
  <div>
    <span class="name">:</span>
    <m-select
      :list="provinceList"
      title=""
      :value="province"
      :showWrapperActive="provinActive"
      @change_active="changeProvinActive"
      @change="changeProvin"
      wclass="province"
    />
    <m-select
      :list="cityList"
      title=""
      :value="city"
      :showWrapperActive="cityActive"
      @change_active="changeCityActive"
      @change="changeCity"
      :disabled="cityDisabled"
      wclass="city"
    />
    <span>:</span>
    <el-select
      v-model="searchWord"
      filterable
      remote
      reserve-keyword
      placeholder=""
      :remote-method="remoteMethod"
      :loading="loading"
    >
      <el-option v-for="item in searchList" :key="item" :label="item" :value="item"></el-option>
    </el-select>
  </div>
</template>

<script>
import MSelect from "./select.vue";
import api from '@/api/index.js'
export default {
  data() {
    return {
      provinceList: ["", "", "", "", "", ""],
      province: "",
      cityList: ["", "", "", ""],
      city: "",
      provinActive: false,
      cityActive: false,
      searchList: ["", "", "", ""],
      searchWord: '',
      loading: false,
      cityDisabled:true
    };
  },
  components: {
    MSelect
  },
  created() {
    api.getProvinceCity().then(res => {
      this.provinceList = res.data.data.map(item => {
        item.name = item.provinceName;
        return item;
      });
      // console.log(res)
    })
  },
  methods: {
    changeProvinActive(flag) {
      if (flag) {
        this.cityActive = false;
      }
      this.provinActive = flag;
    },
    changeCityActive(flag) {
      this.cityActive = flag;
      if (flag) {
        this.provinActive = false;
      }
    },
    changeProvin(item) {
        this.province = item.name;
        this.cityDisabled = false;
        this.cityList = item.cityInfoList;
    },
    changeCity(item) {
        this.city = item.name;
        this.$store.dispatch('setPosition', item);
        this.$router.push({name: 'index'})
    },
    remoteMethod(val) {
        // 
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/changeCity/iselect.scss";
</style>


