<template>
  <div class="m-istyle">
    <dl @mouseover="over" :class="nav.class">
      <dt>{{nav.title}}</dt>
      <dd
        v-for="(item, index) in nav.list"
        :key="index"
        :class="{active:kind == item.tab}"
        :data-type="item.tab"
      >{{item.text}}</dd>
      <!-- <dd :class="{active:kind == 'all'}" data-type="all"></dd>
      <dd :class="{active:kind == 'food'}" data-type="food"></dd>
      <dd :class="{active:kind == 'spa'}" data-type="spa">SPA</dd>
      <dd :class="{active:kind == 'movie'}" data-type="movie"></dd>
      <dd :class="{active:kind == 'travel'}" data-type="travel"></dd>-->
    </dl>
    <ul class="ibody">
      <li v-for="(item, index) in resultsData[kind]" :key="index">
        <el-card :body-style="{ padding: '0px' }" shadow="never">
          <img :src="item.image" class="image">
          <div class="cbody">
            <div class="title" :title="item.title">{{item.title}}</div>
            <div class="sub-title" :title="item.subTitle">{{item.subTitle}}</div>
            <div class="price-info">
              <span class="current-price-wrapper">
                <span class="price-symbol numfont"></span>
                <span class="current-price numfont">{{item.price}}</span>
                <span class="sold bottom-right-info">{{item.address}}</span>
              </span>
              <!-- <span class="old-price">{{item.price_info.old_price}}</span>
              <span class="sold bottom-right-info">{{item.address}}</span> -->
            </div>
            <!-- <div class="price-info" v-else-if="!item.arentNum">
              <span class="current-price-wrapper">
                <span class="price-symbol numfont"></span>
                <span class="current-price numfont"></span>
              </span>
            </div> -->
            <!-- <div class="price-info" v-else>
              <span class="current-price-wrapper">
                <span class="price-symbol numfont"></span>
                <span class="current-price numfont">{{item.price_info.avg_price}}</span>
                <span class="units">/{{item.price_info.units}}</span>
              </span>
            </div> -->
          </div>
        </el-card>
      </li>
    </ul>
  </div>
</template>

<script>
import api from '@/api/index.js'
export default {
  data() {
    return {
      kind: "all",
      resultsData: {},
      list: [
        {
          image:
            "https://p1.meituan.net/wedding/8d26f93de654d433b17774e60a1fc5bd1028431.jpg@240w_180h_1e_1c_1l|watermark=1&&r=2&p=9&x=2&y=2&relative=1&o=20|460w_260h_1e_1c",
          title: "",
          address: "",
          sub_title: "",
          arentNum: 0,
          price_info: {
            current_price: 398,
            old_price: 433,
            avg_price: null,
            units: null,
          }
        },
        {
          image:
            "http://p0.meituan.net/merchantpic/b5bc200ff96056aa58e2441c37b0097d58192.jpg@460w_260h_1e_1c",
          title: "COSTA COFFEE",
          address: "",
          sub_title: "1",
          arentNum: 10,
          price_info: {
            current_price: 11,
            old_price: 18,
            avg_price: null,
            units: null,
          }
        },
        {
          image:
            "http://p0.meituan.net/mogu/fc091566222b23c51857ab4316633e43370925.jpg@460w_260h_1e_1c",
          title: "",
          address: "",
          sub_title: "",
          arentNum: 10,
          price_info: {
            // current_price: 98,
            // old_price: 110,
            current_price: null,
            old_price: null,
            avg_price: 98,
            units: "",
          }
        }
