      all: [
        {
          image:
            "https://p1.meituan.net/wedding/8d26f93de654d433b17774e60a1fc5bd1028431.jpg@240w_180h_1e_1c_1l|watermark=1&&r=2&p=9&x=2&y=2&relative=1&o=20|460w_260h_1e_1c",
          title: "",
          address: "",
          subTitle: "",
          price: 398
        },
        {
          image:
            "http://p0.meituan.net/merchantpic/b5bc200ff96056aa58e2441c37b0097d58192.jpg@460w_260h_1e_1c",
          title: "COSTA COFFEE",
          address: "",
          subTitle: "1",
          price: 11
        },
        {
          image:
            "http://p0.meituan.net/mogu/fc091566222b23c51857ab4316633e43370925.jpg@460w_260h_1e_1c",
          title: "",
          address: "",
          subTitle: "",
          price: 98
        }
      ],
      food: [
        {
          image:
            "http://p0.meituan.net/merchantpic/b5bc200ff96056aa58e2441c37b0097d58192.jpg@460w_260h_1e_1c",
          title: "COSTA COFFEE",
          address: "",
          subTitle: "1",
          price: 11
        },
        {
          image:
            "http://p0.meituan.net/mogu/fc091566222b23c51857ab4316633e43370925.jpg@460w_260h_1e_1c",
          title: "",
          address: "",
          subTitle: "",
          price: 98
        }
      ],
      spa: [
        {
          image:
            "https://p1.meituan.net/wedding/8d26f93de654d433b17774e60a1fc5bd1028431.jpg@240w_180h_1e_1c_1l|watermark=1&&r=2&p=9&x=2&y=2&relative=1&o=20|460w_260h_1e_1c",
          title: "",
          address: "",
          subTitle: "",
          price: 398
        }
      ]
    };
  },
  props: {
    nav: {
      default: {}
    }
  },
  created() {
    api.resultsByKeywords().then(res => {
      this.resultsData = res.data.data;
      // console.log(this.resultsData);
    })
  },
  methods: {
    over(e) {
      // console.log(e.target);
      let dom = e.target;
      let tagName = dom.tagName.toLowerCase();
      //   console.log(tagName)
      if (tagName != "dd") {
        return false;
      }
      this.kind = dom.getAttribute("data-type");
      //  ajax
    }
  }
};
</script>

<style lang="scss">
@import "@/assets/css/index/artistic.scss";
</style>


