<template>
    <div class="m-hcity">
        <dl>
            <dt>{{ title }}</dt>
            <dd v-for="(item, index) in list" :key="index" @click="changeCity(item)">
                {{ item }}
            </dd>
        </dl>
    </div>
</template>

<script>
export default {
    props: [
        "title",
        "list"
    ],
    methods: {
        changeCity(item) {
            // console.log(item, this.list)
        }
    }
}
</script>

<style lang="scss">
    @import "@/assets/css/changecity/hot.scss";
</style>


