</template>

<script>
import MMenu from "@/components/index/menu.vue";
import MLife from "@/components/index/life.vue";
import Container from "@/components/index/container.vue";
export default {
  data() {
    return {
      iStyle: {
        class: "istyle",
        title: "",
        list: [
          {
            text: "",
            tab: "all"
          },
          {
            text: "",
            tab: "food"
          },
          {
            text: "SPA",
            tab: "spa"
          },
          {
            text: "",
            tab: "movie"
          },
          {
            text: "",
            tab: "travel"
          }
        ]
      },
      recomment: {
        class: "youhui",
        title: "",
        list: [
          {
            text: "",
            tab: "all"
          },
          {
            text: "",
            tab: "food"
          },
          {
            text: "",
            tab: "xiuxian"
          }
        ]
      }
    };
  },
  components: {
    MMenu,
    MLife,
    Container
  }
};
</script>

<style lang="scss">
@import "@/assets/css/index/index.scss";
</style>



